import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Tabs, Row, Col, Empty, Image, Avatar, Button, Tooltip} from 'antd'
import {FieldTimeOutlined} from '@ant-design/icons'
import Container from "../../../../components/layout-components/container";
import {useLocation} from "react-router-dom";
import {_GetMyBookingList} from "../../../../redux/actions/booking";
import Loading from "../../../../components/shared-components/Loading";
import Utils from "../../../../utils";
import {first} from "lodash";
import moment from "moment";


const BookingRooms = (props) => {
    const locations = useLocation()
    let {_GetMyBookingList} = props


    const [tabsActive, setTabsActive] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "unpaid"
    })
    const [tabs] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "unpaid"
    })

    function setUp(value) {
        if (window) {

            const urlSearch = new URLSearchParams(window.location.search)
            if (urlSearch.get('tab')) {
                urlSearch.delete('tab')
                urlSearch.append('tab', value)
            } else {
                urlSearch.append('tab', value)
            }
            let query = []
            urlSearch.forEach((value, key) => {
                query.push(`${key}=${value}`)
            })

            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, query.join("&")].join("?"));

        }
    }

    function onTabs(value) {
        setTabsActive(value)

        setUp(value)
    }


    useEffect(() => {
        _GetMyBookingList({
            _room: true,
            limit: 10,
            status: ["waiting"],
            direction: 'desc',
            orderBy: 'book_date'
        })
    }, [_GetMyBookingList])


    return (
        <div className="w-full py-4">
            <Container size={'default'}>
                <Row align="center">
                    <Col xs={22} md={16}>
                        <div className="w-full mb-3 flex justify-between !border-b mb-4 pb-4">
                            <h1 className={'text-xl font-semibold'}>Booking Lists</h1>
                            <div className="text-right">
                                <Button size={'small'} type={'primary'} ghost>Filter</Button>
                            </div>
                        </div>
                        <div>
                            {
                                props?.loading ? (
                                    <Loading cover={'content'}/>
                                ) : (
                                    typeof (props?.data) !== "undefined" && Array.isArray(props?.data) && props?.data.length > 0 ? (
                                        props?.data.map((item, index) => {
                                            return (
                                                <Card bordered={true} loading={props?.loading ?? false}
                                                      key={`booking-lists-${index}`}>
                                                    <div className="flex gap-6">
                                                        <div className="w-60 min-w-40 h-40 rounded-xl overflow-hidden">
                                                            <Avatar
                                                                shape={'square'}
                                                                style={{
                                                                    width: "100%",
                                                                    height: "100%",
                                                                    objectFit: "cover",
                                                                }}
                                                                className={'w-full h-full object-cover'}
                                                                src={item?.room !== null &&
                                                                Array.isArray(item?.room?.images) && item?.room?.images.length > 0 ? (
                                                                        <Image
                                                                            fallback={'/img/image-notfound.png'}
                                                                            style={{
                                                                                height: "10rem",
                                                                                width: "100%",
                                                                                objectFit: "cover",
                                                                            }}
                                                                            src={Utils.ImagePath(first(item?.room?.images)?.prefix_url, first(item?.room?.images)?.filename, '/')}/>
                                                                    ) :
                                                                    <Image
                                                                        fallback={'/img/image-notfound.png'}
                                                                        style={{
                                                                            height: "10rem",
                                                                            width: "100%",
                                                                            objectFit: "cover",
                                                                        }} src={'/img/image-notfound.png'}/>
                                                                }
                                                            >
                                                                {
                                                                    item?.room === null ? "-"
                                                                        :
                                                                        Array.isArray(item?.room?.images) && item?.room?.images.length === 0 ? (item?.room?.name ?? "-") : null
                                                                }
                                                            </Avatar>
                                                        </div>
                                                        <div className="w-full h-100 flex flex-col space-y-4">
                                                            <div className="w-full">
                                                                <div className="w-full flex justify-between">
                                                                    <div className="w-96">
                                                                        <h3 className={'!text-xl font-semibold truncate'}>{item?.room?.name ?? "-"}</h3>
                                                                    </div>
                                                                    {
                                                                        typeof (item?.status) !== "undefined" && (
                                                                            <Button size={'small'} type={'primary'}
                                                                                    ghost>{item?.status}</Button>
                                                                        )
                                                                    }
                                                                </div>
                                                                <div className="flex gap-2 items-center">
                                                                    <Tooltip title={'book date'}>
                                                                        <FieldTimeOutlined className={'text-xl'}/>
                                                                    </Tooltip>
                                                                    <span className={'text-md cursor-default'}>{moment(item?.book_date).format("DD MMMM YYYY") ?? "-"}</span>
                                                                </div>
                                                            </div>

                                                            <div className="w-full flex flex-col space-y-2">


                                                                <div className="flex items-center gap2">
                                                                    <h3 className={'text-lg font-bold'}>{['Rp', Utils.numberFormat(item?.amount, ',')].join("") ?? "0"}</h3>
                                                                    <span>{['/',item?.room?.unit].join('')}</span>
                                                                </div>

                                                                {
                                                                    typeof(item?.status) !== "undefined" && (item?.status === "done" || item?.status === "paid" || item?.status === "berhasil") ? (
                                                                        <div className="">
                                                                            <Button type={'primary'} size={'small'}>Sudah dibayar</Button>
                                                                        </div>
                                                                    ): (
                                                                        <div className="">
                                                                            <Button type={'primary'} size={'small'}>Bayar Sekarang</Button>
                                                                        </div>
                                                                    )
                                                                }

                                                            </div>
                                                        </div>
                                                    </div>
                                                </Card>
                                            )
                                        })
                                    ) : (
                                        <Empty/>
                                    )
                                )
                            }

                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

BookingRooms.propTypes = {
    _GetMyBookingList: PropTypes.func.isRequired
}
BookingRooms.defaultProps = {
    _GetMyBookingList: (params = {}) => {
        console.log({params})
    }
}

export default connect(({Booking}) => {
    let {collections} = Booking
    let {lists} = collections
    return {...lists}
}, {_GetMyBookingList})(React.memo(BookingRooms))